import React, { Component } from 'react';
import './App.css';
import "antd/dist/antd.css";
import { Layout, Menu, Icon,Input } from 'antd';
import HomePage from "./Components/Home";
import firebaseApp from "./firebase.js"
import TopSongs from "./Components/topSongs.js";


const {Header, Content, Footer, Sider} = Layout;


class App extends Component {
  constructor(props){
    super(props);
    this.state={
      songs:"",
      roads:""
    }
  }
  componentDidMount(){
    let x=[];
    let y=[];
    firebaseApp.firestore().collection('songs').get().then(function(snapshot) {
      snapshot.docs.forEach((docSnapshot) => {
        x.push({name:docSnapshot.data().name})
      })
    });
    firebaseApp.firestore().collection('roads').get().then(function(snapshot) {
      
      snapshot.docs.forEach((docSnapshot) => {
        y.push(docSnapshot.data().name)
      })
    });
    this.setState({
      songs:x,
      roads:y
    })
 
  }
  render() {
    return (
      <Layout>
     <Layout style={{ marginLeft: 0 }}>
       <Content style={{width:"100%" ,overflow: 'initial' ,height:"100%"}}>
         <div style={{ padding: 0,height:"100%" }}>
         <HomePage  />
         </div>
       </Content>

     </Layout>
   </Layout>
    );
  }
}

export default App;
