import React, { Component } from 'react';
import { Row,List,Avatar} from 'antd';
import firebaseApp from "../firebase.js"


class TopSongs extends Component {
    constructor(props){
        super(props);
        this.state={
          songs:[],
          roads:""
        }
      }
      componentDidMount(){
        let x=[];
        let y=[];
        
        firebaseApp.firestore().collection('songs').get().then(function(snapshot) {
          snapshot.docs.forEach((docSnapshot) => {
            x.push({name:docSnapshot.data().name,count:docSnapshot.data().count})
          })
        });
        firebaseApp.firestore().collection('roads').get().then(function(snapshot) {
          
          snapshot.docs.forEach((docSnapshot) => {
            y.push(docSnapshot.data().name)
          })
        });
        this.setState({
          songs:x,
          roads:y
        })
      }

    render(){
      return(

            <Row style={{height:"0"}}>
                {/* <ul>
                    {(this.state.songs.map(item => (
                        <li key={item}>{item}</li>
                        )))}
                </ul>
                
                {console.log(this.state.songs)}
                {console.log(this.state.roads)}
                <ul>
                    {setTimeout(function(){return(this.state.songs? this.state.songs.map(item => (
                        <li key={item}>{item}</li>
                        )):console.log("w"))},2000)}
                </ul> */}
                {console.log(this.state.songs)}
                {console.log(this.state.roads)}
            </Row>
                
        )
    }
}

export default TopSongs;